import React, { useState, useEffect } from 'react';
import axios from 'axios';
import ProductList from './ProductList';
import ProductForm from './ProductForm';
import SearchFilter from './SearchFilter';

const App = () => {
    const [products, setProducts] = useState([]);
    const [loading, setLoading] = useState(false);
    const [message, setMessage] = useState({ text: '', type: '' });
    const [editingProduct, setEditingProduct] = useState(null);
    const [searchTerm, setSearchTerm] = useState('');

    // Setup axios with CSRF token
    useEffect(() => {
        const token = document.querySelector('meta[name="csrf-token"]');
        if (token) {
            axios.defaults.headers.common['X-CSRF-TOKEN'] = token.content;
        }
        loadProducts();
    }, []);

    // Load products from API
    const loadProducts = async (search = '') => {
        setLoading(true);
        try {
            const url = search ? `/api/products?search=${encodeURIComponent(search)}` : '/api/products';
            const response = await axios.get(url);
            if (response.data.success) {
                setProducts(response.data.products);
            }
        } catch (error) {
            showMessage('Error loading products: ' + error.message, 'error');
        } finally {
            setLoading(false);
        }
    };

    // Handle search
    const handleSearch = (term) => {
        setSearchTerm(term);
        loadProducts(term);
    };

    // Clear search
    const handleClearSearch = () => {
        setSearchTerm('');
        loadProducts('');
    };

    // Show message
    const showMessage = (text, type) => {
        setMessage({ text, type });
        setTimeout(() => {
            setMessage({ text: '', type: '' });
        }, 5000);
    };

    // Add product
    const handleAddProduct = async (productData) => {
        try {
            const response = await axios.post('/api/products', productData);
            if (response.data.success) {
                showMessage(response.data.message, 'success');
                loadProducts(searchTerm);
                return true;
            }
        } catch (error) {
            const errorMessage = error.response?.data?.message || 'Error adding product';
            showMessage(errorMessage, 'error');
            return false;
        }
    };

    // Update product
    const handleUpdateProduct = async (id, productData) => {
        try {
            const response = await axios.put(`/api/products/${id}`, productData);
            if (response.data.success) {
                showMessage(response.data.message, 'success');
                setEditingProduct(null);
                loadProducts(searchTerm);
                return true;
            }
        } catch (error) {
            const errorMessage = error.response?.data?.message || 'Error updating product';
            showMessage(errorMessage, 'error');
            return false;
        }
    };

    // Delete product
    const handleDeleteProduct = async (id) => {
        if (!window.confirm('Are you sure you want to delete this product?')) {
            return;
        }

        try {
            const response = await axios.delete(`/api/products/${id}`);
            if (response.data.success) {
                showMessage(response.data.message, 'success');
                loadProducts(searchTerm);
            }
        } catch (error) {
            const errorMessage = error.response?.data?.message || 'Error deleting product';
            showMessage(errorMessage, 'error');
        }
    };

    // Edit product
    const handleEditProduct = (product) => {
        setEditingProduct(product);
        // Scroll to form
        document.querySelector('.form-section')?.scrollIntoView({ behavior: 'smooth' });
    };

    // Cancel edit
    const handleCancelEdit = () => {
        setEditingProduct(null);
    };

    return (
        <div className="app">
            <header className="app-header">
                <div className="container">
                    <h1>Product Inventory Manager</h1>
                </div>
            </header>

            <main className="container">
                <div className="inventory-container">
                    {/* Search Filter Section */}
                    <SearchFilter
                        searchTerm={searchTerm}
                        onSearch={handleSearch}
                        onClear={handleClearSearch}
                    />

                    {/* Product Form Section */}
                    <ProductForm
                        editingProduct={editingProduct}
                        onAdd={handleAddProduct}
                        onUpdate={handleUpdateProduct}
                        onCancel={handleCancelEdit}
                    />

                    {/* Message Box */}
                    {message.text && (
                        <div className={`message-box ${message.type}`}>
                            {message.text}
                        </div>
                    )}

                    {/* Products List Section */}
                    <ProductList
                        products={products}
                        loading={loading}
                        onEdit={handleEditProduct}
                        onDelete={handleDeleteProduct}
                    />
                </div>
            </main>

            <footer className="app-footer">
                <div className="container">
                    <p>&copy; {new Date().getFullYear()} Product Inventory Manager</p>
                </div>
            </footer>
        </div>
    );
};

export default App;
